.product {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 0;

  @media (min-width: $desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }

  @media (min-width: $desktop-width) {
    gap: 51px;
  }
}

.product__item {
  border: 1px solid $darken-gray-color;
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    display: flex;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    width: 316px;
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.product__img {
  width: 100%;
  height: 195px;

  @media (min-width: $tablet-width) {
    height: 334px;
  }

  @media (min-width: $desktop-width) {
    height: 462px;
  }
}

.product__title {
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: $dark-color;
  margin: 0;
  margin-bottom: 8px;

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}

.product__text {
  font-weight: 400;
  font-size: 13px;
  line-height: 30px;
  margin: 0;
}

.product__link {
  display: flex;
}

.product__info {
  display: grid;
  padding: 28px 30px 10px 28px;

  @media (min-width: $tablet-width) {
    width: 334px;
    padding-left: 34px;
    padding-top: 27px;
  }

  @media (min-width: $desktop-width) {
    grid-template-rows: min-content 1fr min-content;
    grid-template-columns: 1fr;
    min-height: 196px;
    padding: 26px 28px 20px;
  }
}

.product__price {
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  color: $dark-color;
  border-top: 1px solid $darken-gray-color;
  padding-top: 20px;

  @media (min-width: $tablet-width) {
    margin-top: 179px;
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    max-width: 257px;
    margin: 0;
  }
}

